.header{
   width: 100%;
   z-index: 100;
   position: sticky;
   top: 0;
}

nav{
 max-width: 1200px;
 margin: auto;
 padding: 0 15px;
 display: flex;
 align-items: center;
 justify-content: space-between;
}

.brand__name{
    width: 100px;
    height: 100px;
    background: #fff;
    border-radius: 55px;
}

.item{
    text-decoration: none;
    padding: 10px 15px;
    background: #693b52;
    color: #fff;
    font-size: 13px;
    border-radius: 24px;
}

.nav-right{
    display: flex;
}

.nav-link{
    margin-left: 1rem;
}

.toggle{
    display: flex;
    width: 34px;
    height: 34px;
    background: #693b52;
    color: #fff;
    border-radius: 35px;
    align-items: center;
    justify-content: center;
    font-size: 14px
}

.toggle:focus .sidebar{
  right: 0;
}

@media (max-width: 700px){
    .office-time{
        display: none;
    }
}